@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-montserrat: Montserrat;
  --font-poppins: Poppins;
  --font-inter: Inter;

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-4xl: 23px;
  --font-size-xl: 20px;
  --font-size-3xl: 22px;
  --font-size-7xl: 26px;
  --font-size-lgi: 19px;

  /* Colors */
  --color-whitesmoke-100: #f6f6f6;
  --color-whitesmoke-200: #ececec;
  --color-white: #fff;
  --color-black: #000;
  --color-red: #ff0000;
  --color-limegreen-100: #2cda00;
  --color-silver: #b6b6b6;
  --color-darkslateblue-100: #00448f;
  --color-lightskyblue: #8bd9ff;
  --color-gray-100: #0d0c0d;
  --color-gray-200: rgba(0, 0, 0, 0.5);
  --color-gray-300: rgba(255, 255, 255, 0.12);
  --color-steelblue: #0086c9;
  --color-midnightblue: #30206e;

  /* Border radiuses */
  --br-6xs: 7px;
  --br-4xs: 9px;
  --br-sm: 14px;
  --br-17xl: 36px;
  --br-2xs: 11px;
  --br-35xl: 54px;
  --br-lg: 18px;
  --br-4xl: 23px;
}
